.ant-tabs {
  height: 100%;
}
.ant-tabs-content {
  height: 100%;
}
.ant-tabs-nav {
  width: 300px;
}
.ant-tabs-top > .ant-tabs-nav {
  margin-top: 10px;
  margin-bottom: 0 ;
}
.ant-tabs-tab {
  width: 120px;
  margin: 0 28px 0 0;
  font-weight: 500;
}
.ant-tabs-tab-btn {
  width: 28px;
  position: absolute;
  /* top: 0px; */
  left: 48px;
  font-family: Alibaba PuHuiTi;
  font-weight: Medium;
  font-size: 14px;
  opacity: 1;
  text-align: left;
}
.ant-collapse-content {
  overflow: hidden;
  color: rgba(0, 0, 0, 0.85);
  background-color: #fff;
  /* border-top: 1px solid #ececec; */
  border: 1px solid #979797;
  border-radius: 2px;
  height: 40%;
}
.ant-collapse {
  background-color: #ffffff;
  border: 0px;
}
.ant-collapse > .ant-collapse-item > .ant-collapse-header {
  font-size: 14px;
  padding-left: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: normal;
  width: 285px;
}
.ant-collapse > .ant-collapse-item > .ant-collapse-header .ant-collapse-arrow {
  left: 8px;
}
.ant-collapse-content > .ant-collapse-content-box {
  padding: 5px;
  width: 285px;
}
.SelectedPanel .ant-collapse-content {
  height: 30%;
  overflow-y: auto;
}
.SelectedPanel .ant-collapse-content table {
  border-spacing: 0px 5px;
  border-collapse: separate;
}
.SelectedPanel .ant-collapse-content .ant-table-tbody > tr > td {
  padding: 0px;
  margin-top: 5px;
  background-color: #f1f1f1;
}
.factsheetContent {
  /* padding-top: 10px; */
  text-align: center;
  /* width    : 95%; */
  margin-right: auto;
  margin-left: auto;
}
.factsheetContainer {
  overflow: hidden;
  /* font-family: sans-serif, Arial, Helvetica; */
}
.factsheetTitle {
  display: inline;
  text-align: center;
  font-size: 2.2em;
  color: black;
  outline: none;
}
.factsheetTitle:hover {
  border: dashed 1px #6D757A;
}
.fs_edit div {
  outline: none;
}
.factsheetSlot {
  position: relative;
  display: inline-block;
  border: 0.5px solid #919191;
  vertical-align: top;
  /* height       : 235px; */
  height: 200px;
  margin: 8px;
  border-radius: 1px 1px 1px 1px;
  background-color: white;
  padding-bottom: 5px;
  overflow: hidden;
  cursor: pointer !important;
}
.factsheetSlot:hover {
  box-shadow: 5px 5px 10px rgba(255, 190, 49, 0.801);
}
.factsheetSlot_left_true {
  box-shadow: -10px 0px 18px #ffbe31, /**/ 0px 0px 0px #000, /**/ 0px 0px 0px green, /**/ 0px 0px 0px blue;
  /**/
}
.factsheetSlot_right_true {
  box-shadow: 0px 0px 0px red, /**/ 0px 0px 0px #000, /**/ 10px 0px 18px #FFBE31, /**/ 0px 0px 0px blue;
  /**/
}
.factsheetSlotIn {
  float: left;
  padding-left: 0.2%;
  padding-top: 0.2%;
  margin-top: 10px;
  width: 100%;
}
.factsheetSlotIn .factsheetVis {
  flex: 1 1;
  display: flex !important;
  align-items: center;
  justify-content: center;
}
.factsheetDes {
  width: 30%;
  padding-left: 3%;
  text-align: left;
  font-size: 5%;
  font-family: Arial, Helvetica, sans-serif;
}
.factsheetDes:hover {
  border: dashed 1px #6D757A;
}
.fs_text div {
  outline: none;
}
.factsheetSlotType {
  display: block;
  /* position: absolute;
    top: 10px;
    left: 15px; */
  font-family: "Trebuchet MS", Helvetica, sans-serif;
  color: black;
  text-align: left;
  margin-left: 10px;
}
.factsheetSlotId {
  display: inline;
  position: absolute;
  bottom: 5px;
  right: 15px;
}
.factsheetDes {
  color: black;
}
.factsheetSlotContent {
  /* margin-top: 5px */
  /* height: calc(100% - 21px); */
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.factsheetSlot_2p3 {
  width: calc((90% - 16px) / 3);
}
.factsheetSlot_6p {
  width: calc(90% + 16px);
}
.factsheetSlot_2p {
  width: calc(90% / 3 - 16px / 3);
}
.factsheetSlot_4p {
  width: calc(90% / 3 * 2 + 16px / 3);
}
.factsheetSlot_3p {
  width: calc(90% / 2);
}
/*  */
.factsheetSlot_2p3 .factsheetSlotIn {
  /* float: none;
    margin-left: auto;
    margin-right: auto; */
  float: none;
  display: inline-block;
  vertical-align: middle;
  margin-top: 10px;
}
.factsheetSlot_2p3 .factsheetSlotIn .factsheetSlotContent {
  display: flex;
  justify-content: space-between;
}
.factsheetSlot_2p3 .factsheetSlotIn .factsheetDes {
  width: 45%;
  padding-left: 10px;
  align-self: center;
  font-size: 8px;
  margin-top: 0px;
}
.factsheetSlot_2p3 .factsheetSlotIn .factsheetVis {
  /* flex: 0 1 auto; */
  align-self: center;
  margin-top: 0px;
}
/*  */
.factsheetSlot_3p .factsheetSlotIn {
  float: none;
  display: inline-block;
  vertical-align: middle;
