.factsheetSlot_2p .factsheetSlotIn {
  float: none;
  display: inline-block;
  vertical-align: middle;
  /* margin-top : 10px; */
}
.factsheetSlot_2p .factsheetSlotIn .factsheetSlotContent {
  /* margin-top: 20px; */
  height: 150px;
  display: flex;
  justify-content: space-between;
}
.factsheetSlot_2p .factsheetSlotIn .factsheetDes {
  /* vertical-align: middle; */
